body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-item a {
  color: rgb(21, 21, 100) !important;
  margin: 0 5px;
  font-size: 1.1rem;

}

.blueColor {
  background-color: rgb(21, 21, 100) !important;
}
.textblue {
  color: rgb(21, 21, 100) !important;
}

.nav-item a:hover {
  color: rgb(250, 176, 112) !important;
  font-weight: bold;
}

#Hero-section {
  background-image: linear-gradient(rgba(9, 5, 54, 0.3), rgba(5, 4, 46, 0.7)), url("./Common//imgs//Background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 80vh;

}

.hero-btn button:hover {
  background-color: #fff !important;
  color: rgb(21, 21, 100);
  font-weight: 700;
}